import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import * as ROUTES from "./constants/routes";
import { Container } from "reactstrap";

import {
  EditSchedule,
  GpaCalc,
  SignInPage
} from "./components";
import Navigation from "./components/navigation";
import SignUp from "./components/signup";
import { withAuthentication } from "./components/session";

class App extends Component {
  render() {
    const isLoggedIn = this.props.firebase.getUserId();

    return (
      <Router basename={ROUTES.BASENAME}>
        <>
          <Navigation />
          <Container fluid>
            <Switch>
              <Route exact path={ROUTES.LANDING}>
                <Redirect
                  from="/"
                  to={isLoggedIn ? ROUTES.EDIT_SCHEDULE : ROUTES.SIGN_IN}
                />
              </Route>
              <Route path={ROUTES.SIGN_UP} component={SignUp} />
              <Route path={ROUTES.SIGN_IN} component={SignInPage} />
              {/* <Route path={ROUTES.VIEW_SCHEDULE} component={ViewSchedule} /> */}
              <Route path={ROUTES.EDIT_SCHEDULE} component={EditSchedule} />
              <Route path={ROUTES.GPA_CALC} component={GpaCalc} />
            </Switch>
          </Container>
        </>
      </Router>
    );
  }
}

export default withAuthentication(App);
